"use client";

import React, { useContext } from "react";
import { Card, CardBody, CardFooter, CardHeader } from "@nextui-org/card";
import PropertyCard from "@/components/ripe/PropertyCard/PropertyCard";
import { ViewContext } from "@/components/ripe/ViewBar/ViewProvider";
import { GoogleMap, Marker } from "@react-google-maps/api";
import { useQuery, useMutation } from "@tanstack/react-query";
import { useProperties } from "@/services/properties";
import { Property } from "@/models/property";

export default function Component() {
  const properties = useProperties();

  const defaultMapContainerStyle = {
    width: "100%",
    height: "80vh",
  };

  const mapContainerStyle = {
    width: "100%",
    height: "500px",
  };

  const { view, setView } = useContext(ViewContext);

  const { isPending, isError, data, error } = useQuery({
    queryKey: ["getProperties"],
    queryFn: () => properties.getAll(6, 1, 500),
  });

  if (isPending) {
    return <p>Loading...</p>;
  }
  console.log(data.data);

  return (
    <>
      {view ? (
        <div className="flex w-full">
          <GoogleMap mapContainerStyle={defaultMapContainerStyle}></GoogleMap>
        </div>
      ) : (
        <div className="flex w-full">
          <div className=" grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
            {data.data.properties?.map((property: any, index: number) => {
              if (index < 2) {
                return (
                  <PropertyCard
                    key={property.id}
                    property={property}
                    onClick={function (): void {
                      throw new Error("Function not implemented.");
                    }}
                  ></PropertyCard>
                );
              }
            })}
            <Card className="hidden md:block col-span-1 md:col-span-2 w-full light-mode rounded-md">
              <GoogleMap
                mapContainerClassName="w-full h-full"
                mapTypeId="satellite"
                zoom={3}
                center={{ lat: 33.8588611, lng: -84.2484869 }}
                mapContainerStyle={mapContainerStyle}
              >
                {data.data.properties?.map(
                  (property: Property, index: number) => {
                    const marker = {
                      position: {
                        lat: Number(property.addresses[0].geolocation.latitude),
                        lng: Number(
                          property.addresses[0].geolocation.longitude
                        ),
                      },
                    };
                    return (
                      <Marker
                        {...marker}
                        // onRightClick={() => props.onMarkerRightClick(marker)}
                      />
                    );
                  }
                )}
              </GoogleMap>
            </Card>

            {data.data.properties?.map((property: any, index: number) => {
              if (index > 1) {
                return (
                  <PropertyCard
                    key={property.id}
                    property={property}
                    onClick={function (): void {
                      throw new Error("Function not implemented.");
                    }}
                  ></PropertyCard>
                );
              }
            })}
          </div>
        </div>
      )}
    </>
  );
}

